@font-face {
  font-family: LeagueGothic;
  src: url("./fonts/LeagueGothic-Regular.otf") format("opentype");
}

* {
  font-family: LeagueGothic;
}

.App {
  color: var(--text-color);
  background-color: var(--background);
  --background: #fff;
  --text-color: #000;
}


.App[data-theme="dark"] {
  --background: #121212;
  --text-color: #bfbfbf;
}
